import { SELECT_STOCK, SWITCH_USER, USER_JANE_SMITH } from './constants';

const initialState = {
  user: USER_JANE_SMITH,
  selectedStockName: '',
}

const MicroStrategyWorldReducer = (state = initialState, action = {}) => {
  switch (action.type) {
    case SWITCH_USER:
      return { ...state, user: action.payload };
    case SELECT_STOCK:
      return { ...state, selectedStockName: action.payload };
    default:
      return state;
  }
};
export default MicroStrategyWorldReducer;
