import React, {Fragment} from 'react';

import {
    DropdownToggle, DropdownMenu,
    Nav, Button, NavItem, NavLink,
    UncontrolledTooltip, UncontrolledButtonDropdown, DropdownItem
} from 'reactstrap';

import {
    toast,
    Bounce
} from 'react-toastify';


import {
    faCalendarAlt,
    faAngleDown

} from '@fortawesome/free-solid-svg-icons';

import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

import 'react-toastify/dist/ReactToastify.css';

import avatar1 from '../../../assets/utils/images/avatars/1.jpg';
import avatar2 from '../../../assets/utils/images/avatars/2.jpg';
import avatar3 from '../../../assets/utils/images/avatars/3.jpg';
import { connect } from 'react-redux';
import { USER_ADMIN, USER_JANE_SMITH, USER_JOHN_DOE } from '../../../DemoPages/MicroStrategyWorld/constants';
import { bindActionCreators } from 'redux';
import { selectUser } from '../../../DemoPages/MicroStrategyWorld/selectors';
import { switchUser } from '../../../DemoPages/MicroStrategyWorld/actions';

const UserBox = ({
    onSwitchUser,
    user
}) => {

    const notify2 = () => this.toastId = toast("You don't have any new items in your calendar for today! Go out and play!", {
        transition: Bounce,
        closeButton: true,
        autoClose: 5000,
        position: 'bottom-center',
        type: 'success'
    });

    const showAvatar = (name) => {
        switch (name) {
            case USER_JANE_SMITH.name:
                return avatar1
            case USER_JOHN_DOE.name:
                return avatar2
            case USER_ADMIN.name:
                return avatar3
            default:
                return avatar1
        }
    }


    return (
        <Fragment>
            <div className="header-btn-lg pe-0">
                <div className="widget-content p-0">
                    <div className="widget-content-wrapper">
                        <div className="widget-content-left">
                            <UncontrolledButtonDropdown>
                                <DropdownToggle color="link" className="p-0">
                                    <img width={42} className="rounded-circle" src={showAvatar(user.name)} alt=""/>
                                    <div className="widget-content-left  ms-3 header-user-info d-inline-block">
                                        <div className=".navbar-light .navbar-text">
                                            {user.name}
                                        </div>
                                    </div>
                                    <FontAwesomeIcon className="ms-2 opacity-8" icon={faAngleDown}/>
                                </DropdownToggle>
                                <DropdownMenu end className="rm-pointers dropdown-menu-sm">
                                    <Nav vertical>
                                        <NavItem className="nav-item-header">
                                            Other Profiles
                                        </NavItem>
                                        {user !== USER_JANE_SMITH && <DropdownItem onClick={() => {
                                            onSwitchUser(USER_JANE_SMITH)
                                        }}>
                                            <img width={42} className="rounded-circle" src={avatar1} alt=""/>
                                            <div className="widget-content-left  ms-3 header-user-info d-inline-block">
                                                <div className=".navbar-light .navbar-text">
                                                    Jane Smith
                                                </div>
                                            </div>
                                        </DropdownItem>}
                                        {user !== USER_JOHN_DOE && <DropdownItem onClick={() => {
                                            onSwitchUser(USER_JOHN_DOE)
                                        }}>
                                            <img width={42} className="rounded-circle" src={avatar2} alt=""/>
                                            <div className="widget-content-left  ms-3 header-user-info d-inline-block">
                                                <div className=".navbar-light .navbar-text">
                                                    John Doe
                                                </div>
                                            </div>
                                        </DropdownItem>}
                                        {user !== USER_ADMIN && <DropdownItem onClick={() => {
                                            onSwitchUser(USER_ADMIN)
                                        }}>
                                            <img width={42} className="rounded-circle" src={avatar3} alt=""/>
                                            <div className="widget-content-left  ms-3 header-user-info d-inline-block">
                                                <div className=".navbar-light .navbar-text">
                                                    Admin
                                                </div>
                                            </div>
                                        </DropdownItem>}
                                    </Nav>
                                </DropdownMenu>
                            </UncontrolledButtonDropdown>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    )
}

const mapStateToProps = state => ({
    user: selectUser(state)
});

const mapDispatchToProps = dispatch => {
    return bindActionCreators(
        {
            onSwitchUser: switchUser
        },
        dispatch
    );
};

export default connect(mapStateToProps, mapDispatchToProps)(UserBox);