import React, {Fragment, useState} from 'react';
import {connect} from 'react-redux';

import { Slider } from "react-burgers";

import AppMobileMenu from '../AppMobileMenu';
import adminLogo from '../../assets/utils/images/administrators.png'
import hyperNovaLogo from '../../assets/utils/images/stock logo.png'
import zeniWaveLogo from '../../assets/utils/images/waves.png'
import { USER_ADMIN, USER_JANE_SMITH, USER_JOHN_DOE } from '../../DemoPages/MicroStrategyWorld/constants'
import {
    setEnableClosedSidebar,
    setEnableMobileMenu,
    setEnableMobileMenuSmall,
} from '../../reducers/ThemeOptions';
import { selectUser } from '../../DemoPages/MicroStrategyWorld/selectors';

class HeaderLogo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            active: false,
            mobile: false,
            activeSecondaryMenuMobile: false,
        };

    }

    toggleEnableClosedSidebar = () => {
        let {enableClosedSidebar, setEnableClosedSidebar} = this.props;
        setEnableClosedSidebar(!enableClosedSidebar);
    }

    state = {
        openLeft: false,
        openRight: false,
        relativeWidth: false,
        width: 280,
        noTouchOpen: false,
        noTouchClose: false,
    };
    
    render() {
        let appLogo = this.props.user.name === USER_JOHN_DOE.name ? zeniWaveLogo: (this.props.user.name === USER_ADMIN.name ? adminLogo : hyperNovaLogo);
        let appName = this.props.user.name === USER_JOHN_DOE.name ? "ZeniWave": (this.props.user.name === USER_ADMIN.name ? "Administrator" : "Hypernova");
        let {
            enableClosedSidebar,
        } = this.props;

        const {
        } = this.state;
        return (
            <Fragment>
                <div className="app-header__logo">
                    <div className="logo-src" background>
                    
                        <img className='logo-src-image' src={appLogo}/>
                        
                        <span className='logo-src-label'>{appName}</span>
                    </div>
                    <div className="header__pane ms-auto">
                        <div onClick={this.toggleEnableClosedSidebar}>
                            <Slider
                                active={enableClosedSidebar}
                                type="elastic"
                                onClick={() => this.setState({active: !this.state.active})}
                            />
                        </div>
                    </div>
                </div>
                <AppMobileMenu/>
            </Fragment>
        )
    }
}


const mapStateToProps = state => ({
    enableClosedSidebar: state.ThemeOptions.enableClosedSidebar,
    enableMobileMenu: state.ThemeOptions.enableMobileMenu,
    enableMobileMenuSmall: state.ThemeOptions.enableMobileMenuSmall,
    user: selectUser(state)
});

const mapDispatchToProps = dispatch => ({

    setEnableClosedSidebar: enable => dispatch(setEnableClosedSidebar(enable)),
    setEnableMobileMenu: enable => dispatch(setEnableMobileMenu(enable)),
    setEnableMobileMenuSmall: enable => dispatch(setEnableMobileMenuSmall(enable)),

});

export default connect(mapStateToProps, mapDispatchToProps)(HeaderLogo);