import React, {Fragment, useEffect, useState} from 'react';

// Layout

import AppHeader from '../../Layout/AppHeader';
import AppSidebar from '../../Layout/AppSidebar';

import PageTitle from '../../Layout/AppMain/PageTitle';
import { connect } from 'react-redux';
import { selectStockName, selectUser } from './selectors';
import { bindActionCreators } from 'redux';
import { selectStock } from './actions';
import { useHistory } from "react-router-dom";
import DateFilter from './Investment/DateFilter';

const Investment = ({
  user,
  onSelectStock,
}) => {

  let history = useHistory();

  const [dossierInstance, setDossierInstance] = useState(undefined);
  const [dateFilterKey, setDateFilterKey] = useState(undefined);

  const embedDossier = (user) => {
    let url = user.investmentDossierURL;
    let dossier; // Variable to store the dossier created. Used by Event Handler do not remove!
    let config; // Variable to store the configuration settings for dossier.
    async function runCode() {
        // For more details on configuration properties, see https://microstrategy.github.io/embedding-sdk-docs/add-functionality/methods-and-properties
        config = {
        url: url,
        placeholder: document.getElementById("embedding-dossier-container"),
        containerHeight: "600px",
        containerWidth: "800px",
        customAuthenticationType:
            window.microstrategy.dossier.CustomAuthenticationType.AUTH_TOKEN,
        customUi: {},
        disableNotification: true,
        dockedComment: {
            dockedPosition: "left",
            canClose: true,
            dockChangeable: false,
            isDocked: false,
        },
        dockedFilter: {
            dockedPosition: "left",
            canClose: true,
            dockChangeable: false,
            isDocked: false,
        },
        dockedTOC: {
            dockedPosition: "left",
            theme: "light",
            canClose: true,
            dockChangeable: false,
            isDocked: false,
        },
        dossierFeature: {
            readonly: false,
        },
        enableCollaboration: true,
        enableCustomAuthentication: false,
        enableResponsive: true,
        filterFeature: {
            enabled: false,
            edit: true,
            summary: true,
        },
        filters: [],
        getLoginToken: function login() {
            console.log("Implement log in to return promise of auth token");
        },
        navigationBar: {
            enabled: false,
            gotoLibrary: true,
            title: true,
            toc: true,
            reset: true,
            reprompt: true,
            share: true,
            comment: true,
            notification: true,
            filter: true,
            options: true,
            search: true,
            bookmark: true,
            edit: false,
        },
        optionsFeature: {
            enabled: true,
            help: false,
            logout: true,
            manage: false,
            showTutorials: false,
        },
        shareFeature: {
            enabled: true,
            invite: false,
            link: true,
            email: false,
            export: true,
            download: false,
            shareDossier: false,
            subscribe: false,
        },
        smartBanner: false,
        tocFeature: {
            enabled: true,
        },
        uiMessage: {
            enabled: true,
            addToLibrary: false,
        },
        visibleTutorials: {
            library: true,
            welcome: false,
            dossier: true,
            notification: false,
        },
        visualizationAppearances: [],
        };
        // INSERT PROPERTIES BELOW HERE

        // INSERT PROPERTIES ABOVE HERE

        // Embed the dossier with the configuration settings
        try {
          dossier = await window.window.microstrategy.dossier.create(config);
          setDossierInstance(dossier);
          responsiveHeight()
        } catch (error) {
        // console.error(error);
        }

        // INSERT METHODS BELOW HERE
        /* On Graphics Selected Event Start*/
        function eventGraphicsSelectedFunction(e) {
          // Add any functionality for event needed here
          if (!e.graphics || !e.graphics[0] || !e.graphics[0][0]) {
            return
          }
          if (['K170', '90AE8B7F60DC4CF4A69E725ADD82AB67'].includes(e.vizKey) && e.graphics[0][0].n === 'Stock') {
            onSelectStock(e.graphics[0][0].v)
            history.push('/trading-center');
          }
        }
        dossier.registerEventHandler(
          window.microstrategy.dossier.EventType.ON_GRAPHICS_SELECTED,
          eventGraphicsSelectedFunction
        );

        // To remove the event handler, use the code in comment below
        // dossier.removeEventHandler(microstrategy.dossier.EventType.ON_GRAPHICS_SELECTED, eventGraphicsSelectedFunction);
        /* On Graphics Selected Event End*/
        // INSERT METHODS ABOVE HERE

        function responsiveHeight() {
          //Assuming the embedded dossier is the last element on the page,
          //this code will help you resize the embedded dossier to always
          //fill the remaining space of the page dynamically.

          //The container of the dossier
          const container = document.getElementById(
            "embedding-dossier-container"
          );
          //The distance from the bottom border to the bottom of the page.
          const marginBottom = 8;
          //The minimal height is the remaining space.
          container.style.minHeight =
            "calc(100vh - " + (container.offsetTop + marginBottom) + "px)";
        }

        dossier
          .getFilterList()
          .then((filterList) => {
            filterList.forEach((filter) => {
              if (filter.filterName === 'Date') {
                setDateFilterKey(filter.filterKey)
              }
            })
          })
          .catch((error) => {
            console.error(error);
          });
    }
    runCode();
  }

  useEffect(() => {
    embedDossier(user)
  }, [user])

  return (
    <Fragment>
        <AppHeader/>
        <div className="app-main">
            <AppSidebar/>
            <div className="app-main__outer">
                <div className="app-main__inner">
                <div style={{display: 'flex', flexFlow: 'row', alignItems: 'center', justifyContent: 'space-between'}}>
                  <PageTitle
                      className="d-inline-block"
                      heading="My Portfolio"
                      subheading="Updated on April 30th, 16:46:27"
                      icon="pe-7s-wallet icon-gradient bg-arielle-smile"
                  />
                  <DateFilter className="d-inline-block" dossierInstance={dossierInstance} dateFilterKey={dateFilterKey}/>
                </div>
                <div id="embedding-dossier-container" />
                </div>
            </div>
        </div>
    </Fragment>
  );
}

const mapStateToProps = state => ({
  user: selectUser(state),
  selectedStockName: selectStockName(state),
});

const mapDispatchToProps = dispatch => {
  return bindActionCreators(
      {
        onSelectStock: selectStock
      },
      dispatch
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(Investment);