import React, { Fragment, useEffect, useState } from "react";
// Layout

import AppHeader from "../../Layout/AppHeader";
import AppSidebar from "../../Layout/AppSidebar";
import AppFooter from "../../Layout/AppFooter";

import PageTitle from "../../Layout/AppMain/PageTitle";
import visa1 from "./images/visa1.png";
import visa2 from "./images/visa2.png";

import {
  Button,
  Form,
  FormGroup,
  Label,
  Input,
  FormText,
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  ButtonGroup,
  ButtonToolbar,
} from "reactstrap";
import { selectStockName } from "./selectors";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { useHistory } from "react-router-dom";

const TradingCenter = ({ selectedStockName }) => {
  const [tradeSelected, setTradeSelected] = useState(1);
  const [investInSelected, setInvestInSelected] = useState(1);
  const [numberOfShares, setNumberOfShares] = useState(1);
  const [stockIndex, setStockIndex] = useState(0);
  const fund = 100000;

  const history = useHistory();

  //A list of stocks
  const stocks = [
    ["AAL", "American Airlines Group, Inc", 11.41],
    ["AAPL", "Apple Inc", 67.38],
    ["ALGT", "Allegiant Travel Company", 75.13],
    ["ALK", "Alaska Air Group, Inc", 29.19],
    ["AMZN", "Amazon.com, Inc", 2221.59],
    ["BCS", "Barclays PLC", 4.56],
    ["BHC", "Bausch Health Companies Inc", 16.46],
    ["CS", "Credit Suisse Group", 8.38],
    ["DAL", "Delta Air Lines, Inc", 23.96],
    ["DB", "Deutsche Bank AG", 6.49],
    ["GOOG", "Alphabet Inc (Google) Class C", 1229.59],
    ["GS", "Goldman Sachs Group, Inc. (The)", 175.13],
    ["HA", "Hawaiian Holdings, Inc", 10.99],
    ["IBM", "International Business Machines Corporation", 118.78],
    ["JNJ", "Johnson & Johnson", 146.02],
    ["LUV", "Southwest Airlines Company", 31.99],
    ["META", "Meta Platforms Inc", 174.88],
    ["MRK", "Merck & Company, Inc", 80.81],
    ["MS", "Morgan Stanley", 37.87],
    ["MSFT", "Microsoft Corp.", 168.1],
    ["PFE", "Pfizer, Inc", 35.73],
    ["RHHBY", "Roche Holding Ltd", 42.32],
    ["UNH", "UnitedHealth Group Incorporated", 270.22],
    ["WFC", "Wells Fargo & Company", 28.04],
  ];

  useEffect(() => {
    stocks.forEach((stock, index) => {
      if (stock[0] === selectedStockName) {
        setStockIndex(index);
      }
    });
  }, []);

  return (
    <Fragment>
      <AppHeader />
      <div className="app-main">
        <AppSidebar />
        <div className="app-main__outer">
          <div className="app-main__inner">
            <PageTitle
              heading="Trading Center"
              subheading="Select stocks to buy or sell"
              icon="pe-7s-graph2 icon-gradient bg-arielle-smile"
            />
            <Card className="main-card mb-3">
              <CardBody>
                <Form>
                  <FormGroup row>
                    <Label for="exampleEmail" sm={2}>
                      Trade
                    </Label>
                    <Col sm={10}>
                      <ButtonToolbar>
                        <ButtonGroup>
                          <Button
                            className="mb-2 me-3"
                            color={
                              tradeSelected === 1 ? "warning" : "secondary"
                            }
                            outline
                            onClick={() => setTradeSelected(1)}
                            active={tradeSelected === 1}
                          >
                            Buy
                          </Button>
                        </ButtonGroup>
                        <ButtonGroup>
                          <Button
                            className="mb-2 me-3"
                            color={
                              tradeSelected === 2 ? "warning" : "secondary"
                            }
                            outline
                            onClick={() => setTradeSelected(2)}
                            active={tradeSelected === 2}
                          >
                            Sell
                          </Button>
                        </ButtonGroup>
                      </ButtonToolbar>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={2}>Available Fund</Label>
                    <Col sm={10}>
                      <Label sm={2}>$ 100,000 </Label>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="" sm={2}>
                      Stock
                    </Label>
                    <Col sm={10}>
                      <FormGroup>
                        <Label for="stockSelector">Select</Label>
                        <Input
                          id="stockSelector"
                          name="select"
                          type="select"
                          onChange={(event) => {
                            setStockIndex(event.target.selectedIndex);
                          }}
                        >
                          {stocks.map(function (stock) {
                            return (
                              <option
                                selected={stock[0] === selectedStockName}
                                key={stock[0]}
                              >
                                {stock[0]} - {stock[1]}
                              </option>
                            );
                          })}
                        </Input>
                      </FormGroup>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="" sm={2}>
                      Invest in
                    </Label>
                    <Col sm={10}>
                      <ButtonToolbar>
                        <ButtonGroup>
                          <Button
                            className="mb-2 me-3"
                            color={
                              investInSelected === 1 ? "warning" : "secondary"
                            }
                            outline
                            onClick={() => setInvestInSelected(1)}
                            active={investInSelected === 1}
                          >
                            Shares
                          </Button>
                        </ButtonGroup>
                      </ButtonToolbar>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label for="shares" sm={2}>
                      {investInSelected === 1 ? "Shares" : "Dollars"}
                    </Label>
                    <Col sm={10}>
                      <Input
                        bsSize="lg"
                        id="shares"
                        name="shares"
                        // placeholder="1"
                        type="number"
                        value={numberOfShares}
                        min={1}
                        max={fund / stocks[stockIndex][2]}
                        onChange={(event) => {
                          console.log(event);
                          setNumberOfShares(event.target.value);
                        }}
                      />
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={2}>Market Price</Label>
                    <Col sm={10}>
                      <Label sm={2}>${stocks[stockIndex][2]}</Label>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={2}>Commission</Label>
                    <Col sm={10}>
                      <Label sm={2}>Free</Label>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    <Label sm={2}>Estimated Cost</Label>
                    <Col sm={{ size: 10 }}>
                      <Label sm={2}>
                        $ {(numberOfShares * stocks[stockIndex][2]).toFixed(2)}
                      </Label>
                    </Col>
                  </FormGroup>
                  <FormGroup row>
                    {/* <Col sm={{size:30}}> */}
                    <Label sm={2}></Label>
                    <Button
                      color="primary"
                      style={{
                        width: 200,
                      }}
                      onClick={() => {
                        history.push("/investment");
                      }}
                    >
                      PLACE ORDER
                    </Button>

                    {/* </Col> */}
                  </FormGroup>
                </Form>
              </CardBody>
            </Card>
          </div>
          <AppFooter />
        </div>
      </div>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  selectedStockName: selectStockName(state),
});

const mapDispatchToProps = (dispatch) => {
  return bindActionCreators({}, dispatch);
};

export default connect(mapStateToProps, mapDispatchToProps)(TradingCenter);
