import { createSelector } from 'reselect';

export const selectMicroStrategyWorld = (state) => state.MicroStrategyWorld;

export const selectUser = createSelector(
  selectMicroStrategyWorld,
  (MicroStrategyWorld) => MicroStrategyWorld.user,
);

export const selectStockName = createSelector(
  selectMicroStrategyWorld,
  (MicroStrategyWorld) => MicroStrategyWorld.selectedStockName
);