import React, { useState } from 'react';
import { Button, ButtonGroup } from 'reactstrap';

function DateFilter(props) {
  const [rSelected, setRSelected] = useState(1);

  const dossierInstance = props.dossierInstance
  const dateFilterKey = props.dateFilterKey;



  function updateDateFilter(dossier, period) {

    let today = new Date()
    const  toDate = today.toLocaleDateString('en-US', {year: 'numeric', month: '2-digit', day: '2-digit'});
    let fromDate = today;

    if (period == '1D') {
      today.setDate(today.getDate() - 1);
    } else if (period == '1M') {
      today.setMonth(today.getMonth() - 1);
    } else if (period == '6M') {
      today.setMonth(today.getMonth() - 6);
    } else if (period == '1Y') {
      today.setMonth(today.getMonth() - 12);
    } else if (period == 'All') {
      today = undefined
    }

    /* Set Date Range for the Filter Start */
    dossier.filterSetDateRange({
      filterInfo: {
        key: dateFilterKey, // Replace with the key of the filter that is being edited.
      },
      date: {
        from: today === undefined ? undefined : today.toLocaleDateString('en-US', {year: 'numeric', month: '2-digit', day: '2-digit'}), // Replace with the date you want to filter from.
        to: toDate, // Replace with the date you want to filter to.
      },
      holdSubmit: false,
    });
    /* Set Date Range for the Filter End */
  }

  return (
    <div>
      <ButtonGroup>
        <Button
          color="primary"
          outline
          onClick={() => {
            setRSelected(1)
            updateDateFilter(dossierInstance, '1D')
          }}
          active={rSelected === 1}
        >
          1D
        </Button>
        <Button
          color="primary"
          outline
          onClick={() => {
            setRSelected(2)
            updateDateFilter(dossierInstance, '1M')
          }}
          active={rSelected === 2}
        >
          1M
        </Button>
        <Button
          color="primary"
          outline
          onClick={() => {
            setRSelected(3)
            updateDateFilter(dossierInstance, '6M')
          }}
          active={rSelected === 3}
        >
          6M
        </Button>
        <Button
          color="primary"
          outline
          onClick={() => {
            setRSelected(4)
            updateDateFilter(dossierInstance, '1Y')
          }}
          active={rSelected === 4}
        >
          1Y
        </Button>
        <Button
          color="primary"
          outline
          onClick={() => {
            setRSelected(5)
            updateDateFilter(dossierInstance, 'All')
          }}
          active={rSelected === 5}
        >
          All
        </Button>
      </ButtonGroup>
    </div>
  );
}

export default DateFilter;